import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'JEmily - Cannon Beach';

  timeUntilMoveIn: number = -1;
  timeUntilJourneyStart: number = -1;

  moveInDay = new Date(2020, 5, 23);
  leaveDay = new Date(2020, 5, 20);

  daysToMove: number;
  hoursToMove: number;
  minutesToMove: number;
  secondsToMove: number;

  daysToStart: number;
  hoursToStart: number;
  minutesToStart: number;
  secondsToStart: number;

  isMobile: boolean;

  ngOnInit() {
    this.isMobile = window.innerWidth < 768;

    setInterval(() => {
      let now = new Date();

      this.timeUntilMoveIn = Math.round(
        Math.abs(this.moveInDay.getTime() - now.getTime()) / 1000
      );
      this.timeUntilJourneyStart = Math.round(
        Math.abs(this.leaveDay.getTime() - now.getTime()) / 1000
      );

      [
        this.daysToMove,
        this.hoursToMove,
        this.minutesToMove,
        this.secondsToMove
      ] = this.getVals(this.timeUntilMoveIn);

      [
        this.daysToStart,
        this.hoursToStart,
        this.minutesToStart,
        this.secondsToStart
      ] = this.getVals(this.timeUntilJourneyStart);
    }, 1000);
  }

  getVals(delta: number): number[] {
    var days = Math.floor(delta / 86400);
    delta -= days * 86400;

    var hours = Math.floor(delta / 3600) % 24;
    delta -= hours * 3600;

    var minutes = Math.floor(delta / 60) % 60;
    delta -= minutes * 60;

    var seconds = delta % 60; // in theory the modulus is not required

    return [days, hours, minutes, seconds];
  }
}
